.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-size: cover !important;
  transition: background 0.3s ease-in-out;
}

.player {
  width: 550px;
  min-height: 650px;
  height: 65%;
  background: #f9f9f9;
  border-radius: 10px;
  position: relative;
  padding: 30px 30px;
  display: flex;
  flex-flow: column;
}

.player-control__top {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: space-between;
}

.play-pause-text > strong {
  font-weight: 400;
  font-size: 14px;
  letter-spacing: 2px;
  margin-right: 10px;
  color: #333;
}

.play-pause-text > span {
  font-size: 14px;
  font-weight: 700;
  color: #333;
}

.player-control__top > button {
  width: 32px;
  height: 32px;
  display: block;
  background: transparent;
  outline: none;
  border: none;
  padding: 5px;
  padding-right: 0;
  padding-left: 10px;
}

.player-control__top > button > svg {
  fill: tomato;
}

.player-track__title {
  font-size: 20px;
  color: #333;
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 2ch;
  margin-top: 0.5ch;
  max-height: 9ch;
  overflow: auto;
}

.player-center {
  /* position: absolute; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* top: 50%;
  left: 50%; */
  /* transform: translateX(-50%) translateY(-60%); */
  flex: 1;
  /* overflow: hidden; */
}

.player-control__btm {
  height: 120px;
  /* position: absolute; */
  /* bottom: 30px;
  left: 30px;
  right: 30px; */
}

.lrc {
  overflow: scroll !important;
}

.lrc::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.progress-bar {
  width: 100%;
  height: 3px;
  border-radius: 100000px;
  overflow: hidden;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  transition: 0.1s ease-in-out;
}

/* .progress-bar:hover {
  height: 6px;
} */

.progress-bar > .done {
  /* background: #333; */
  border-radius: 100000px;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  /* right: 100%; */
  z-index: 11;
}

.progress-bar > .remaining {
  background: #333;
  opacity: 0.2;
  border-radius: 100000px;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}

.progress-container {
  position: relative;
}

.progress-container > .handle-ctr {
  position: absolute;
  width: 0px;
  overflow: visible;
  transform: translateY(-65%);
  /* left: 50px; */
}

.progress-container .handle-ctr .time-span {
  display: inline-block;
  font-size: 1rem;
  letter-spacing: 2px;
  font-weight: bold;
  text-align: center;
  background: #333;
  color: #fff;
  /* color: #000; */
  margin-left: 50%;
  text-align: center;
  width: auto;
  max-width: 30vw;
  padding: 4px 4px 4px 5px;
  transform: translateX(-50%);
  margin-bottom: 20px;
  position: relative;
  transition: all 0.1s ease-in-out;
}

.progress-container .handle-ctr .time-span span {
  position: absolute;
  left: 50%;
  top: 100%;
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
  width: 6px;
  height: 6px;
  background: #333;
}

.progress-container .handle-ctr .handle {
  display: block;
  width: 40px;
  background: transparent;
  outline: none;
  border: none;
  margin-left: 50%;
  transform: translateX(-50%);
}

.player-control__container {
  margin-top: 35px;
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: space-around;
}

.player-nav__btm {
  margin-top: 30px;
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: space-between;
}

.emptyList {
  display: flex;
  flex: 1;
  flex-flow: column;
  justify-content: center;
  align-items: center;
}

.emptyList > h4 {
  color: #333;
  margin-top: 15px;
}

.tooltip-container {
  position: relative;
  z-index: 10;
}

.tooltip-box {
  position: absolute;
  font-weight: 600;
  color: #fff;
  padding: 3px 5px;
  border-radius: 5px;
  top: calc(100% + 10px);
  z-index: 10;
  white-space: pre-wrap;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.2s ease-in-out;
}

.tooltip-badge {
  position: absolute;
  top: -2px;
  right: -7px;
  background: red;
  font-size: 11px;
  width: 16px;
  height: 16px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: 600;
}

.tooltip-box.visible {
  opacity: 1;
  visibility: visible;
}
